import React, { useEffect, useMemo, useState } from 'react';
import classes from './MyPosts.module.css';
import Posts from './Posts';
import Prilouder from '../coummon/Prilouder/Prilouder';
import AddPostForm from './AddPostForm';



const MyPosts = (props) => {

    let posts = props.posts;
    let addPostC = props.addPostAC;

    const [postsstate, setposts] = useState([]);


    let postsData = useMemo(() => {
        return [...posts].reverse()
    }, [posts])

    useEffect(() => {
        setposts(postsData)
    }, [postsData])

    return (
        <div>
            {!props.profile ?
                <div style={{ marginLeft: '216px', width: "778px", height: "400px", boxShadow: "0 0 2.5px -1px black", borderRadius: "10px", padding: "50px 0 0 0", backgroundColor: "white" }}>
                    <h2 style={{ opacity: 0.2 }}>загрузка постов</h2>
                    <Prilouder />
                </div> :
                <div className={classes.MyPosts}>
                    <div className={classes.MyPostsForm}>
                        <AddPostForm addPostC={addPostC} {...props} />
                    </div>
                    <div className={classes.publications}>
                        <div className={classes.Post}>
                            {postsstate.map((p) => <Posts key={p.id} message={p.message} likes={p.likes} id={p.id} deletePost={props.deletePost} {...props} />)}
                        </div>
                    </div>
                </div>}
        </div>
    )
};


export default MyPosts;