import React from 'react';
import { useForm } from 'react-hook-form';
import classes from './MyPosts.module.css';
import ava from '../../assets/avaImg/mal2.jpg';
import { type } from 'os';

const AddPostForm = (props) => {

    const {
        register,
        handleSubmit,
        formState: { errors }
    } = useForm({ mode: 'onChange' });

    function onSubmit(data) {
        props.addPostC(data.post, data.post + new Date() + Math.random() * 5)
    }

    if (!props.profile) {
        return null
    }

    return (
        <>
            {props.isOuner ?
                <form onSubmit={handleSubmit(onSubmit)}>
                    <div className={classes.textareaButtonBlock}>
                        <img src={props.profile.photos.large ? props.profile.photos.large : ava} alt="user" />
                        <div className={classes.textareaButton}>
                            <textarea
                                name='post'
                                ref={register({ required: 'пустое поле', maxLength: 700 })}
                                cols="65"
                                placeholder={errors.post ? `error: ${errors.post.message}` : 'ваш пост'}
                                className={errors.post ? classes.redBorder : ''}
                            />
                            <button className={errors.post?.type == 'maxLength' ? `${classes.buttonForm} ${classes.redBorder}` : `${classes.buttonForm}`}>{errors.post?.type == 'maxLength' ? `лимит в 700 эл` : "Опубликовать"}</button>
                        </div>
                    </div>
                </form> :
                null}
        </>

    )
};

export default AddPostForm;