import React from 'react';
import classes from './ProfileInfo.module.css'

import { useForm } from 'react-hook-form';

const ProfileDataForm = (props) => {

    const {
        register,
        handleSubmit,
        formState: { errors }
    } = useForm({ mode: 'onChange' });

    function onSubmit(data) {
        data.userId = props.profile.userId
        data.photos = props.profile.photos
        props.saveInfo(data)
    }

    const profile = props.profile

    return (
        <>
            <form onSubmit={handleSubmit(onSubmit)}>
                <div className={classes.profileForm}>
                    <div>
                        <span className={classes.item}>
                            Полное имя
                        </span>
                        <div>
                            <input placeholder="полное имя" name="fullName" defaultValue={profile.fullName} ref={register} />
                        </div>
                    </div>
                    <hr />
                    <div>
                        <span className={classes.item}>
                            Обо мне
                        </span>
                        <div>
                            <textarea placeholder="о себе" name="aboutMe" defaultValue={profile.aboutMe} ref={register} cols="70" rows="5" />
                        </div>
                    </div>
                    <hr />
                    <div>
                        <span className={classes.item}>
                            Ищю работу
                        </span>
                        <div>
                            <input type="checkbox" name="lookingForAJob" defaultChecked={profile.lookingForAJob} ref={register} />
                        </div>
                    </div>
                    <hr />
                    <div>
                        <span className={classes.item}>
                            Что умеете
                        </span>
                        <div>
                            <textarea placeholder="профессиянальные навыки" defaultValue={profile.lookingForAJobDescription} name="lookingForAJobDescription" ref={register} cols="70" rows="5" />
                        </div>
                    </div>
                    <hr />
                    <span className={classes.item}>Контакты</span>
                    {Object.keys(profile.contacts).map((contact, index) => <div key={index} className={classes.formInputs}>
                        <div>
                            <input placeholder={contact} defaultValue={profile.contacts[contact]} name={"contacts." + contact} ref={register} />
                        </div>
                    </div>)}
                    <div className="error">
                        {props.error && props.error}
                    </div>
                    <hr />
                    <button>Сохранить</button><label onClick={props.editFormInfo}>вернуться</label>
                </div>
            </form>
        </>
    )
};

export default ProfileDataForm;
